import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { HttpHeaders } from '@angular/common/http';

import { Observable } from 'rxjs';
import { catchError } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})

export class FyndService {

  	constructor(private http: HttpClient) { }

	/** GET movies from the server */
  	searchMovies(term: string): Observable<any[]> {
		const url = `http://localhost:8000/fynd/movies/search?search=`;
    	return this.http.get<any[]>(url+ term);
  	}

	getMovies(): Observable<any[]> {
		const url = `http://localhost:8000/fynd/movies/search?search=How`;
		return this.http.get<any>(url);
	}
}
